@import url(https://fonts.googleapis.com/css?family=Lato:100&display=swap);
body{margin:0;font-family:'Lato', sans-serif;-webkit-font-smoothing:antialiased;-moz-osx-font-smoothing:grayscale;background-color:#070707;color:white}

div.app div.calculator-wrapper{display:flex;flex-direction:column;justify-content:center;align-items:center;text-align:center;min-height:100vh}

div.calculator{width:360px;height:450px;background-color:red;border-style:solid;border-width:1px;border-color:coral;border-radius:5px;display:grid;grid-template-columns:repeat(4, 90px);grid-template-rows:repeat(5, 90px);grid-column-gap:0px;grid-row-gap:0px;box-shadow:0px 0px 15px 5px rgba(255,127,80,0.45)}.screenWrapper{grid-area:1 / 1 / 2 / 5}.btnDivideWrapper{grid-area:2 / 4 / 3 / 5}.btnMultiplyWrapper{grid-area:3 / 4 / 4 / 5}.btnAddWrapper{grid-area:4 / 4 / 5 / 5}.btnSubtractWrapper{grid-area:5 / 4 / 6 / 5}.btnClearWrapper{grid-area:5 / 1 / 6 / 2}.btnEqualWrapper{grid-area:5 / 3 / 6 / 4}.btnNumber1Wrapper{grid-area:2 / 1 / 3 / 2}.btnNumber2Wrapper{grid-area:2 / 2 / 3 / 3}.btnNumber3Wrapper{grid-area:2 / 3 / 3 / 4}.btnNumber4Wrapper{grid-area:3 / 1 / 4 / 2}.btnNumber5Wrapper{grid-area:3 / 2 / 4 / 3}.btnNumber6Wrapper{grid-area:3 / 3 / 4 / 4}.btnNumber7Wrapper{grid-area:4 / 1 / 5 / 2}.btnNumber8Wrapper{grid-area:4 / 2 / 5 / 3}.btnNumber9Wrapper{grid-area:4 / 3 / 5 / 4}.btnNumber0Wrapper{grid-area:5 / 2 / 6 / 3}

div.btn{width:90px;height:90px;background-color:white;color:black;display:table-cell;vertical-align:middle;text-align:center;border-style:solid;border-width:1px;border-color:coral;font-weight:bold;cursor:pointer;-webkit-transition:.5s;transition:.5s}div.btnNumber{font-size:20px}div.btnNumber:hover{background-color:#666;color:white}div.btnOperation{font-size:24px;background-color:coral;color:white}div.btnOperation:hover{background-color:#ff632a}div.btnClear{text-transform:lowercase;color:rgba(95,95,95,0.6)}div.btnClear:hover{background-color:#ff632a;color:white}

div.screen{width:360px;height:90px;background-color:white;color:black;display:table-cell;vertical-align:middle;text-align:right;border-style:solid;border-width:1px;border-color:coral;font-weight:bold;-webkit-transition:.5s;transition:.5s;padding:0px 20px;font-size:25px}

